<template>
    <header class="header">
        <router-link to="/new"
                     class="logo"
                     exact>
            <svg class="svg svg--navigation"
                 width="30px"
                 height="30px"
                 viewBox="0 0 256 256">
                <use xlink:href="#pcIconLogo"
                     class="svg__use"></use>
            </svg>
            <span>patternCode</span>
        </router-link>

        <nav class="nav">
            <item-button v-bind:icon="true"
                         v-bind:title="'New Pattern'"
                         v-on:click="handleNew"
                         v-bind:disabled="disabledNew">
                <svg class="svg svg--navigation" width="24" height="24" viewBox="0 0 24 24">
                    <use class="svg__use"
                         xlink:href="#pcIconNew"></use>
                </svg>
            </item-button>

            <item-button v-bind:icon="true"
                         v-bind:disabled="disableSave"
                         v-on:click="savePattern"
                         title="Save">
                <svg class="svg svg--navigation" width="24" height="24" viewBox="0 0 24 24">
                    <use class="svg__use"
                         xlink:href="#pcIconSave"></use>
                </svg>
            </item-button>

            <router-link to="/Settings" class="nav__justify-right link-icon">
                <svg class="svg svg--settings"
                     width="24"
                     height="24"
                     viewBox="0 0 24 24">
                    <use class="svg__use"
                         xlink:href="#pcIconSettings"></use>
                </svg>
            </router-link>
        </nav>

        <div class="search">
            <!-- <item-button class="button-menu"
                         v-bind:title="'Menu'"
                         v-bind:transparent="true"
                         v-on:click="handleMenu">
                <svg v-if="isMenuActive"
                     class="svg svg--close"
                     width="24"
                     height="24"
                     viewBox="0 0 24 24">
                    <use class="svg__use"
                         xlink:href="#close">
                    </use>
                </svg>
                <svg v-else
                     class="svg svg--menu"
                     width="24"
                     height="24"
                     viewBox="0 0 24 24">
                    <use class="svg__use"
                         xlink:href="#menu">
                    </use>
                </svg>
            </item-button> -->
        </div>
    </header>
</template>

<script>
import ItemButton from '../items/Button.vue';

export default {
    name: 'component-header',

    components: {
        ItemButton
    },

    computed: {
        disabledNew () {
            if (this.$route.name === 'pattern-new') {
                return true;
            }

            const store = this.$store;

            if (store && store.state &&
                store.state.pattern &&
                store.state.pattern.current) {
                return !store.getters['pattern/current/edited'];
            }

            return false;
        },

        /**
         * Watches if save button should be disabled or not.
         * First check if store.pattern.current is avaible.
         *
         * @method disableSave
         * @return {Boolean} Returns false if nothing to save
         */
        disableSave () {
            const store = this.$store;

            if (store && store.state &&
                store.state.pattern &&
                store.state.pattern.current) {
                return store.getters['pattern/current/edited'];
            }

            return false;
        }
    },

    methods: {
        handleNew () {
            this.$router.push({ path: '/new' });
        },

        /**
         * Save pattern, or open modal if name is not setted.
         *
         * @method savePattern
         * @fires store dispatch pattern/current/savePattern
         * @fires router push to this new saved pattern
         */
        async savePattern () {
            const url = await this.$store.dispatch('pattern/current/savePattern');

            if (url) {
                this.$router.push({ path: `/${url}` });
            }
        }
    }
};
</script>

