<template>
    <aside class="aside">
        <nav class="aside__nav"
             v-bind:class="{ 'aside__nav--active': asideActive }">
            <item-button class="button-menu"
                         v-bind:title="'Menu'"
                         v-bind:transparent="true"
                         v-on:click="handleMenu">
                <svg v-if="asideActive"
                     class="svg svg--close"
                     width="24"
                     height="24"
                     viewBox="0 0 24 24">
                    <use class="svg__use"
                         xlink:href="#pcIconClose">
                    </use>
                </svg>
                <svg v-else
                     class="svg svg--menu"
                     width="24"
                     height="24"
                     viewBox="0 0 24 24">
                    <use class="svg__use"
                         xlink:href="#pcIconMenu">
                    </use>
                </svg>
            </item-button>
        </nav>
        <ul v-if="asideActive"
            class="aside__list">
            <label class="aside__label">Patterns</label>
            <li v-for="pattern in patterns" :key="pattern.id">
                <router-link v-on:click.native="closeAside"
                             :to="{ path: '/' + pattern.url }">
                    {{pattern.name}}
                </router-link>
            </li>
            <form class="form"
                  autocomplete="off"
                  v-on:submit.prevent>
                <item-input v-if="newPattern"
                            id="inputNameList"
                            v-bind:left="true"
                            v-bind:error="inputError.display"
                            v-on:input="nameInputHandler"
                            v-model="inputPatternName"
                            autocomplete="off"
                            autofocus>
                </item-input>
                <label
                    v-if="newPattern && inputError.display && inputError.empty"
                    for="inputNameList"
                    class="label label--error">
                    Filename can not be empty. Please choose an unique file name.
                </label>
                <label
                    v-if="newPattern && inputError.display && inputError.usedName"
                    for="inputNameList"
                    class="label label--error">
                    Please choose an unique file name.
                </label>
            </form>
        </ul>
    </aside>
</template>

<script>
import ItemButton from '../items/Button.vue';
import ItemInput from '../items/Input.vue';

export default {
    name: 'component-aside-list',

    components: {
        ItemButton,
        ItemInput
    },

    data () {
        return {
            isClickMenu: false,
            inputPatternName: null,
            inputError: {
                display: false,
                empty: false,
                usedName: false
            }
        };
    },

    async created () {
        this.checkMediaQuery();
        window.addEventListener('resize', this.resizeAside);
    },

    async mounted () {
        await this.getAllByCreated();
    },

    computed: {
        asideActive () {
            return this.$store.getters['aside/active'];
        },

        patterns () {
            return this.$store.getters['pattern/list/all'];
        },

        newPattern () {
            return this.$route.path === '/new';
        }
    },

    methods: {
        async handleMenu () {
            this.isClickMenu = true;
            console.log('handleMenu');
            await this.$store.dispatch('aside/changeActive');
            window.dispatchEvent(new Event('resize'));
            this.isClickMenu = false;
        },

        async closeAside () {
            console.log('closeAside');
            /* if (!window.matchMedia('(min-width: 700px)').matches && this.asideActive) {
                this.handleMenu();
            } */
        },

        newHandler () {
            this.$router.push({ path: '/new' });
        },

        async getAllByCreated () {
            await this.$store.dispatch('pattern/list/setPatterns');
        },

        async nameInputHandler () {
            // trim whitespace at start
            this.inputPatternName = this.inputPatternName.replace(/^\s+/g, '');
            // trim whitespace at end
            let trimmedPatternName = this.inputPatternName.replace(/\s+$/g, '');

            if (!this.inputPatternName || this.inputPatternName === '') {
                this.inputError.display = true;
                this.inputError.empty = true;
            } else {
                const nameAvailable = await this.$store
                    .getters['pattern/current/nameAvailable'](trimmedPatternName);

                if (!nameAvailable) {
                    this.inputError.display = true;
                    this.inputError.usedName = true;

                    trimmedPatternName = '';
                } else {
                    this.inputError.display = false;
                    this.inputError.empty = false;
                    this.inputError.usedName = false;
                }
            }

            await this.$store.dispatch('pattern/current/setName', {
                name: trimmedPatternName
            });
        },

        resizeAside () {
            console.log('resizeAside 1');
            if (!this.isClickMenu && !window.matchMedia('(min-width: 700px)').matches && this.asideActive) {
                console.log('resizeAside 2', 'inside if');
                this.handleMenu();
            }

            if (!this.isClickMenu && window.matchMedia('(min-width: 700px)').matches && !this.asideActive) {
                console.log('resizeAside 3', 'inside if');
                this.handleMenu();
            }
        },

        checkMediaQuery () {
            console.log('checkMediaQuery 1', window.matchMedia('(min-width: 700px)').matches);
            if (window.matchMedia('(min-width: 700px)').matches && !this.asideActive) {
                console.log('checkMediaQuery 2', 'inside if');
                this.handleMenu();
            }
        }
    },

    async beforeDestroy () {
        document.removeEventListener('resize', this.resizeAside);
    }
};
</script>

