<template>
    <aside class="aside">
        <icons-edit></icons-edit>
        <nav class="nav">
            <item-button v-bind:transparent="true"
                         v-bind:disabled="disabledDelete"
                         v-on:click="deletePattern"
                         title="Delete">
                <svg class="svg svg--navigation" width="24" height="24" viewBox="0 0 24 24">
                    <use class="svg__use"
                         xlink:href="#delete"></use>
                </svg>
            </item-button>
        </nav>
    </aside>
</template>

<script>
import ItemButton from '../items/Button.vue';
import IconsEdit from '../assets/icons/Edit.vue';

export default {
    name: 'component-aside-edit',

    components: {
        ItemButton,
        IconsEdit
    },

    computed: {
        /**
         * Watches if save button should be disabled or not.
         *
         * @method disableSave
         * @return {Boolean} Returns false if nothing to save
         */
        disableSave () {
            return this.$store.getters['pattern/current/edited'];
        },

        /**
         * Watches if delete button should be disabled or not.
         *
         * @method disabledDelete
         * @return {Boolean} Returns false if nothing to delete
         */
        disabledDelete () {
            if (typeof this.$route.params.id !== 'undefined') {
                return false;
            }
            return true;
        }
    },

    methods: {
        /**
         * Save pattern, or open modal if name is not setted.
         *
         * @method savePattern
         * @fires store dispatch pattern/current/savePattern
         * @fires router push to this new saved pattern
         */
        async savePattern () {
            const url = await this.$store.dispatch('pattern/current/savePattern');

            if (url) {
                this.$router.push({ path: `/${url}` });
            }
        },

        /**
         * Open modal to delete pattern.
         *
         * @method disabledDelete
         * @fires store dispatch modal/openPatternDelete
         */
        async deletePattern () {
            await this.$store.dispatch('modal/openPatternDelete');
        }
    }
};
</script>

