.aside {
    grid-area: edit;
    display: flex;
    flex-direction: column;
}

.nav {
    position: fixed;
    top: calc(38px + 48px);
    right: 0;
    display: flex;
    flex-direction: column;
    height: calc(100vh - 38px - 48px);
}

.hr {
    width: 60%;
    height: 1px;
    border: 0;
    background-color: var(--color-label);
}

.hr--last {
    margin-top: auto;
    margin-bottom: 5px;
}
</style>
